/* Lawyer Skils */
.Lawyer-card-skils {
    background-color: transparent;
    width: 300px;
    height: 200px;
    perspective: 1000px;
  }
  
  .Lawyer-card-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.6s;
    transform-style: preserve-3d;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  }
  
  .Lawyer-card-skils:hover .Lawyer-card-inner {
    transform: rotateY(180deg);
  }
  
  .Lawyer-card-front, .Lawyer-card-back {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
  }
  
  .Lawyer-card-front {
    padding: 45px;
    border-radius: 3px;
    border: solid 1px #c4c4c4;
    background-color: #ffffff;
    color: #08b9e3;
  }
  
  .Lawyer-card-back {
    border-radius: 3px;
    border: solid 2px #08b9e3;
    background-color: #fff;
    color: #08b9e3;
    transform: rotateY(180deg);
  }

  .heart-i{
    font-size: xx-large;
    border: 2px solid;
    border-radius: 50%;
    padding: 7px;
  }
/* Lawyer Skils End */

/* Lawyers list cards */
.image img{
    height: 100%;
    width: 100%;
}

.lw-wrapper{
padding: 20px;
display: grid;
grid-template-columns: repeat(auto-fit, minmax(230px, -webkit-max-content));
grid-template-columns: repeat(auto-fit, minmax(230px, max-content));
grid-gap:30px;
justify-content: center;
}


.lw-card{
    background-color: #ffff;
    box-shadow: 10px 10px 30px rgba(0,0,0,0.5);
    padding: 20px;
    border-radius: 15px;
    text-align: center;
    position: relative;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    transition: 0.5s;
}

.lw-card:hover{
cursor: pointer;
transform: translateY(-10px);
}

.lw-card::before{
    content: '';
position: absolute;
top: 0;
left: 0;
height: 600px;
width: 100%;
background-color: #08b9e3;
-webkit-clip-path: circle(260px at 50% -20%);
        clip-path: circle(260px at 50% -20%);
}

.card-head{
    position: relative;
    height: 200px;
    display:flex;
    justify-content: center;
    align-items: center;
}

.lw-card .lw-image{
background-position:center;
background-size: cover;
background-repeat: no-repeat;
height: 120px;
width:120px;
border-radius: 50%;
border: 6px solid #fff;
}

.card1 .lw-image{
    background-image:url(data:image/jpeg;base64,);
}

.card1 .lw-image{
    background-image:url(data:image/jpeg;base64,);
}

.card1 .lw-image{
    background-image:url(data:image/jpeg;base64,);
}


